.brands{
    width:100%;
     height:100%
}

.slider-control-centerleft{
    position: absolute;
    top: 50%;
    left: 0px;
    transform: translateY(-50%);
}

.slider-control-buttonleft{
    border: 0px;
    background: rgba(0, 0, 0, 0.4);
    color: white;
    padding: 10px;
    opacity: 1;
    cursor: pointer;
}


.slider-control-centerright{
    position: absolute;
    top: 50%;
    right: 0px;
    transform: translateY(-50%);
}

.slider-control-buttonright{
    border: 0px;
    background: rgba(0, 0, 0, 0.4);
    color: white;
    padding: 10px;
    opacity: 1;
    cursor: pointer;
}

@media (max-width:500px)
{
    .slider-control-bottomcenter{
        display: none
    }

    .slider-control-centerleft{
        position: absolute;
        top: 25%;
        left: 0px;
        transform: translateY(-50%);
    }

    .slider-control-buttonleft{
        border: 0px;
        background: rgba(0, 0, 0, 0.4);
        color: white;
        padding: 6px;
        opacity: 1;
        cursor: pointer;
    }

    .slider-control-centerright{
        position: absolute;
        top: 25%;
        right: 0px;
        transform: translateY(-50%);
    }

    .slider-control-buttonright{
        border: 0px;
        background: rgba(0, 0, 0, 0.4);
        color: white;
        padding: 6px;
        opacity: 1;
        cursor: pointer;
    }

}

